import axios, { AxiosError } from 'axios'
import { SERVER_URI } from '../config';
import { ResultForm } from './resultForm'
import { resultProps } from './resultForm';
import {ChangeEvent, useState, FormEvent, useEffect} from 'react';
import { loremIpsum } from 'lorem-ipsum';


export function Info(){
    const defaultResult: resultProps = {
        classOfWebSite: " ",
        tags: ['1', '2','3','4','5'],
        fullText: loremIpsum({ count: 3 }),
        shortenedText: loremIpsum({ count: 7 })
    }
    const [siteUrl, setSiteUrl] = useState('');
    const [loading, setLoading] = useState(false); 
    const [error, setError] = useState('');
    const [res, setRes] = useState(defaultResult);

    useEffect( () => {}
    )


    const changeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        try{
            if (event.target.value) {
                setError('');
                setLoading(false);
                setSiteUrl(event.target.value);
            }
        }catch(er){
            console.log(er);
        }
    }

    const submitHandler = async(event: FormEvent) => {
        event.preventDefault();
        if (siteUrl === ''){
            setError('');
            setLoading(false);
            console.log(siteUrl);
            setRes(defaultResult);
            return;
        }
        setSiteUrl('');
        try {
            setError('');
            setLoading(true);
            /* post   */
            const params = new URLSearchParams([['path', siteUrl]]);
            await axios.get(SERVER_URI, {params})
                .then((response) => {
                    const result: resultProps = {
                        classOfWebSite: response.data.body.class,
                        tags: response.data.body.tags,
                        fullText: response.data.body.article,
                        shortenedText: response.data.body.short
                    }
                    console.log(response.data); 
                    setRes(res);
                });
                
            setLoading(false);
            }
        catch(er)
        {
            setLoading(false);
            const error = er as AxiosError;
            setError(error.message);
            console.log('Error', error.message);
            return;
        }
    };



    return(
        <>
        <div className="flex-box flex-auto text-white">
              <div>
                {!error && loading && <div>.......</div>}
                {error && <div className='bg-red-700'> {error}</div>}
                {!error && !loading && <ResultForm tags={res.tags} classOfWebSite={res.classOfWebSite} fullText={res.fullText} shortenedText={res.shortenedText}></ResultForm>}

            </div>
        </div>


        <div className="w-[30vw] h-[30vh]  text-[3vmin]">
            
            <form className="grid" method="post" onSubmit={submitHandler}> 
                <input className="form-control me-2 text-center" defaultValue='' value={siteUrl} onChange={changeHandler} type="input" placeholder="  " aria-label="Input"/>
                <div className="flex items-left justify-center"><button className="btn btn-dark  text-[3vmin]" type="submit"></button></div>
            </form>
        </div>
        </>
    );
}